export default {
	data() {
		return {
			statusBarHeight: 0, // 
			myback: {
				// 
				show: true,
				// 
				imgUrl: require('../../static/images/ico_back.png')
			},
			// top
			topStyle: {}
		}
	},
	props: {
		// 
		title: String,
		// 
		titleColor: {
			type: String,
			default: '#FFFFFF'
		},
		// 
		back: {
			type: Object,
			default: function() {
				return {
					// 
					show: true,
					// 
					imgUrl: require('../../static/images/ico_back.png')
				}
			}
		},
		// backgroundImage
		backgroundImageShow: {
			type: Boolean,
			default: false
		},
		// linear-gradient(to top right, #CDDC39, #8BC34A, #FFEB3B);
		backgroundColor: {
			type: String,
			default: '#004799'
		},
		// backgroundImageShowtrue
		backgroundImage: String,
		// 
		topHeight: {
			type: Number,
			default: 108
		},
	},
	mounted() {
		this.init();
	},
	methods: {
		goBack() {
			uni.navigateBack();
		},
		init() {















			this.statusBarHeight = this.getSysInfo().statusBarHeight;
			for (let key in this.back) {
				this.myback[key] = this.back[key];
			}
		},
		getSysInfo() {
			return uni.getSystemInfoSync();
		}
	}
}
