/**
 * 
 *  (...? T^T)
 * 
 * ...$on $off
 * 
 * 
 */

// 
let isListen = false;
// 
const callbackList = [];

let accX, accY, accZ;

// 
let oldDirection = -1;

/**
 * 
 * @param {Function} callback 
 * 	callback : 0-, 90-90, -90-90
 */
export function onDirectionChange(callback) {
	if (!typeof(callback) === 'function') {
		// callback.constructor === Function ()
		throw new Error('callback is not a function');
	}
	// 
	const index = callbackList.findIndex(cb => (cb === callback));
	if (index === -1) {
		callbackList.push(callback);
	}
	if (isListen) {
		return;
	}
	uni.onAccelerometerChange((res) => {
		const { x, y, z } = res;
		accX = x, accY = y, accZ = z;
		// console.log('---  --->', { x: x.toFixed(1), y: y.toFixed(1), z: z.toFixed(1) });
		/**
		 * : y <= -0.9
		 * 		requestFullScreen direction = 0
		 * : x  >= 0.9
		 * 		x <= -0.9  -> direction =  90
		 * 		x >=  0.9  -> direction = -90
		 */
		let direction = -1;
		if (y <= -0.6) {
			direction = 0;
		} else if (x <= -0.6) {
			direction = 90;
		} else if (x >= 0.6) {
			direction = -90;
		}
		
		if (direction !== -1 && oldDirection != direction) {
			// ( || ) &&  =>  callback 
			callbackList.forEach((cb) => {
				cb(direction);
			});
		}
		oldDirection = direction;
	});
	// 
	startAccelerometer();
}

/**
 * 
 * @param {Function} callback
 */
export function offDirectionChange(callback) {
	let stop = false;
	if (typeof(callback) === 'function') {
		const index = callbackList.findIndex(cb => (cb === callback));
		if (index !== -1) {
			callbackList.splice(index, 1);
		}
	} else {
		stop = true;
	}
	if (callbackList.length === 0) {
		stop = true;
	}
	if (stop && isListen) {
		// callback || list -> 
		uni.offAccelerometerChange(function (res) {
			console.log('', res);
		});
		// 
		stopAccelerometer();
	}
}

/**
 * @description 
 *  uni.startAccelerometer(OBJECT)
 */
function startAccelerometer () {
	return new Promise((resolve, reject) => {
		uni.startAccelerometer({
			interval: 'normal', // 'game','normal','ui'
			success: (res) => {
				console.log(res);
				resolve();
			},
			fail: (err) => {
				console.log(err);
				reject();
			}
		});
	})
}

/**
 * @description 
 *  uni.stopAccelerometer(OBJECT)
 */
function stopAccelerometer () {
	return new Promise((resolve, reject) => {
		uni.stopAccelerometer({
			success: (res) => {
				console.log(res);
				resolve();
			},
			fail: (err) => {
				console.log(err);
				reject();
			}
		})
	});
}
