// cache-tool.js
export default {
  // 
  set(key, value) {
    return new Promise((resolve, reject) => {
      uni.setStorage({
        key,
        data: value,
        success() {
          resolve();
        },
        fail(err) {
          reject(err);
        },
      });
    });
  },

  // 
  get(key) {
    return new Promise((resolve, reject) => {
      uni.getStorage({
        key,
        success(res) {
          resolve(res.data);
        },
        fail(err) {
          reject(err);
        },
      });
    });
  },

  // 
  remove(key) {
    return new Promise((resolve, reject) => {
      uni.removeStorage({
        key,
        success() {
          resolve();
        },
        fail(err) {
          reject(err);
        },
      });
    });
  },

  // 
  containsKey(key) {
    return new Promise((resolve) => {
      uni.getStorageInfo({
        success(info) {
          const keys = info.keys;
          resolve(keys.includes(key));
        },
      });
    });
  },
};

