/*  */
export function apiOrderData(params) {
	return new Promise((resolute, reject)=>{
		//,
		setTimeout(()=> {
			try{
				if(!params.pageNum){
					params.pageNum == 1
				}
				if(!params.pageSize){
					params.pageSize == 10
				}
				const {
					pageNum,pageSize
				} = params
				let data = {
					list: [], // 
					totalCount: 0, // 
					totalPage: 0, // 
					hasNext: false // 
				}
				// let data = orders
				// 
				for (let i = (pageNum - 1) * pageSize; i < pageNum * pageSize; i++) {
					if (i >= orders.length) break
					data.list.push(orders[i])
				}
				resolute(data);
			} catch (e) {
				//
				reject(e);
			}
		},1000)
	})
}